import React, { Component } from 'react';
import styles from "./Dashboard.module.css"
import Button from '@material-ui/core/Button';
import {logoutAction} from "../../store/actions/loginAction";
import {connect} from "react-redux"
import {changeCurrentPageName} from "../../store/actions/userPreference"

 class Dashboard extends Component {

    componentDidMount() {
        this.props.changeCurrentPageName(this.props.location.pathname)
    }
    

    render() {
        return (
            <div className={styles.container}>
                <div className={styles.LoginBox}>
                <div className={styles.LoginBox_Input}>
                     <Button 
                     onClick={this.props.logout}
                     variant="contained" 
                     color="primary" 
                     style={{marginTop:15}} >
                        Log out
                    </Button>
                </div>
                </div>
            </div>
        )
    }
};


const mapDispatchToProps = dispatch => {
    return{
        logout:()=>dispatch(logoutAction()),
        changeCurrentPageName:(name)=>dispatch(changeCurrentPageName(name))
    }
}

export default connect(null,mapDispatchToProps)(Dashboard)
